@import url('https://fonts.googleapis.com/css2?family=Sora:wght@100;200;300;400;500;600;700;800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cinzel:wght@400;500;600;700;800;900&family=Poiret+One&display=swap');


*{
  outline: none;
  -webkit-tap-highlight-color: transparent;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  margin: 0;
  padding: 0;
}
html{
  height: 100%;
  overflow-x: hidden;
  scroll-behavior: smooth;
}
body::-webkit-scrollbar {
  width: 0px;
}
body {
  --cbl: #00b3ff;
  --cbd: #2F85AD;

  --crl: #FA5066;
  --crd: #AD2638;

  --cyl: #FAFFE8;
  --cyd: #FAF469;

  --cgl: #1fdb38;
  --cgd: #248d32;

  --cp: #00b3ff;
  --cs: #e38e44;

  --fsss: 'Sora', sans-serif;
  --fssp: 'Poiret One', sans-serif;
  --fsc: 'Cinzel', serif;

  --xxsf: clamp(0.6rem, 2vw, 0.8rem);
  --xsf: clamp(0.8rem, 2vw, 1rem);
  --sf: clamp(1rem, 2vw, 1.2rem);
  --mf: clamp(1.3rem, 4vw, 1.8rem);
  --bf: clamp(2rem, 6vw, 3.5rem);

  position: relative;
  overflow-x: hidden;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

